import "core-js/modules/es.object.to-string.js";
import "core-js/modules/web.dom-collections.for-each.js";
import "core-js/modules/es.array.iterator.js";
import "core-js/modules/web.dom-collections.iterator.js";
import VuexGenerator from '@ctsj/vuexgenerator';
console.log('VuexGeneratorPlugin.js');
/**
 * serviceRegister
 */
function serviceRegister() {
  var requireComponent = require.context('../../service', false, /.*\.(js)$/);
  var services = {};
  requireComponent.keys().forEach(function (fileName) {
    var serviceKey = fileName.substring(2, fileName.length - 3);
    services[serviceKey] = requireComponent(fileName);
  });
  return services;
}
/**
 * modelRegister
 */
function modelRegister() {
  var requireComponent = require.context('../modules', false, /.*\.(js)$/);
  var models = {};
  requireComponent.keys().forEach(function (fileName) {
    var modelKey = fileName.substring(2, fileName.length - 3);
    models[modelKey] = requireComponent(fileName);
  });
  return models;
}
// 创建VuexGeneratorPlugin插件
export default VuexGenerator(serviceRegister(), modelRegister());